    justify-content: center;
    align-items: center;
}

.list-item-img-style{
    max-width: 100px;
    -webkit-transition: all 0.5s ease-out 0s;
    transition: all 0.5s ease-out 0s;
    /*width: auto;*/
    /*height: auto;*/
    /*max-width: 100%;*/
    /*max-height: 100%;*/
}

.list-item-img-style:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
