.login-container{
    padding: 0 10px 0 10px;
}

.login{
    /*width: 250px;*/
    margin-top: 50px;
    display: flex;
    flex-direction: column;
}

.login-user{
    display: flex;
    position: relative;
}

.login-user input{
    width: 100%;
    padding-left: 18px;
    margin: 5px 0 5px 0;
    border-radius: 6px;
    outline: white;
    border: 1px solid #999;
    height: 30px;
    font-size: large;
    letter-spacing: 2px;
}

.login-user i{
    position: absolute;
    top: 13px;
    left: 5px;
    color: #999;
    font-size: 14px;

}

.login-phone{
    display: flex;
    position: relative;
}

.login-phone input{
    width: 100%;
    padding-left: 18px;
    margin: 5px 0 5px 0;
    border-radius: 6px;
    outline: white;
    border: 1px solid #999;
    height: 30px;
    font-size: large;
    letter-spacing: 1px;
}

.login-phone a{
    position: absolute;
    right: 20px;
    top: 12px;
    color: #481ecc;
    font-size: 14px;
    text-decoration: none;
}

.login-phone i{
    position: absolute;
    top: 13px;
    left: 5px;
    color: #999;
    font-size: 14px;
}

.login button{
    border: 1px solid #999;
    background-color: red;
    border-radius: 20px;
    height: 40px;
    /*outline: none;*/
    width: 100%;
    color: white;
    font-size: 20px;
    font-weight: 300;
    letter-spacing: 10px;
}

.login input:hover{
    box-shadow: inset 0 -1px 1px #3b6caa,inset 1px 0 1px #3b6caa,inset -1px 0 1px #3b6caa;
}

